html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3 {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

img,
video {
  display: block;
  height: auto;
  max-width: 100%;
}

body {
  margin: 0;
  line-height: 32px;
  font-weight: 400;
  font-size: 14px;
}
